import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/Home.vue'
import Sign from '../components/Sign.vue'

Vue.use(VueRouter)

const routes = [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/sign',
      name: 'sign',
      component: Sign,
    }
  ]
  
  const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
  })
  
  export default router

